  display: table;
}
dl.automatewoo-meta-data:after {
  clear: both;
}
dl.automatewoo-meta-data dt,
dl.automatewoo-meta-data dd {
  display: inline-block;
  float: left;
  margin: 0 0 0.8em;
  padding: 0;
}
dl.automatewoo-meta-data dt {
  font-weight: bold;
  clear: left;
  padding-right: 0.5em;
}
dl.automatewoo-meta-data dd p:last-child {
  margin-bottom: 0;
}
dl.automatewoo-meta-data a {
  text-decoration: none;
}

body.post-type-aw_workflow #wpbody-content h1:before,
.automatewoo-page h1:before {
  content: "";
  display: inline-block;
  width: 27px;
  height: 20px;
  background-image: url("../img/header-badge.svg");
  background-size: contain;
  background-repeat: no-repeat;
  margin-right: 9px;
  position: relative;
  top: 2px;
}

body.post-type-aw_workflow #wpbody-content h1:before {
  margin-right: 4px;
}

#wpbody-content #wp__notice-list + .automatewoo-page:not(.woocommerce) {
  padding-top: 96px;
}

.automatewoo-content--has-sidebar {
  *zoom: 1;
}
.automatewoo-content--has-sidebar:before, .automatewoo-content--has-sidebar:after {
  content: " ";
  display: table;
}
.automatewoo-content--has-sidebar:after {
  clear: both;
}
@media (min-width: 1450px) {
  .automatewoo-content--has-sidebar .automatewoo-main {
    float: left;
    width: calc(100% - 275px);
  }
  .automatewoo-content--has-sidebar .automatewoo-sidebar {
    float: right;
    margin-top: 42px;
    width: 250px;
    margin-left: 25px;
  }
}

.automatewoo-sidebar {
  color: #959595;
  border-top: 1px solid #ddd;
  padding: 18px 0 5px;
  margin-top: 13px;
}
.automatewoo-sidebar p {
  margin: 0 0 9px;
}
@media (min-width: 1450px) {
  .automatewoo-sidebar p {
    margin: 0 0 13px;
  }
}
.automatewoo-sidebar p a {
  color: #959595;
}

.automatewoo-arrow-link {
  display: block;
  width: 14px;
  height: 14px;
  border: 2px solid #d1d1d1;
  border-radius: 50%;
  text-decoration: none;
  color: #d1d1d1;
  text-align: center;
  position: relative;
}
.automatewoo-arrow-link:before {
  display: inline-block;
  font: 400 18px/1 dashicons;
  content: "\f345";
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: top;
  position: absolute;
  top: 1px;
  left: 2px;
  font-size: 13px;
  -webkit-font-smoothing: subpixel-antialiased;
}
.automatewoo-arrow-link:hover {
  color: #464646;
  border-color: #464646;
}

.automatewoo-plugin-table-update-message {
  display: block;
  background: #d54d21;
  color: #ffffff;
  padding: 1em;
  margin: 9px 0;
}
.automatewoo-plugin-table-update-message a {
  color: #ffffff;
  text-decoration: underline;
}
.automatewoo-plugin-table-update-message:before {
  display: inline-block;
  font: 400 18px/1 dashicons;
  content: "\f348";
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: top;
  margin: 0 8px 0 -2px;
}

[data-automatewoo-show] {
  display: none;
}

.automatewoo-help-tip {
  font-size: 15px;
  color: #a9a9a9;
}

.automatewoo-help-link--right,
.automatewoo-help-tip--right {
  float: right;
  position: relative;
  right: -2px;
}

.automatewoo-tiptip {
  cursor: help;
}

.automatewoo-help-link:before {
  display: inline-block;
  font: 400 18px/1 dashicons;
  content: "\f504";
  speak: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  vertical-align: top;
  color: #bbbbbb;
  font-size: 16px;
}
.automatewoo-help-link:hover:before {
  color: #0085ba;
}

.automatewoo-badge--blocked-email {
  display: inline-block;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  color: #ffffff;
}
.automatewoo-badge--blocked-email .dashicons {
  font-size: 12px;
  height: 14px;
  width: 17px;
  margin-top: 3px;
}

.automatewoo-badge {
  margin: 0 6px;
}
.automatewoo-badge--warning {
  color: #ca4a1f;
}
.automatewoo-badge--warning .dashicons {
  font-size: 21px;
}
.automatewoo-badge--blocked-email {
  background: #ca4a1f;
}

.aw_system_check_table td.help {
  position: relative;
}

.aw-hidden {
  display: none !important;
}